:root {
  --link-color: #00acdc;
  --header-height: 60px;
}

body {
  margin: 0;
  font-family: Roboto, sans-serif;
}

.header {
  height: var(--header-height);
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.nav_list {
  text-transform: uppercase;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav_line {
  height: var(--header-height);
  align-items: center;
  display: flex;
  position: relative;
}

.img_logo {
  width: 40px;
  height: 40px;
}

.nav_link {
  color: #000;
  margin-right: 20px;
  text-decoration: none;
  position: relative;
}

.nav_link:hover {
  color: var(--link-color);
}

.is-active {
  background-color: var(--link-color);
  border-radius: 25%;
  width: 0;
  height: 4px;
  position: absolute;
  bottom: 0;
}

.nav_link:hover + .is-active {
  width: 100%;
}
